*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: Montserrat !important; 
  -webkit-font-family: Montserrat !important; 
}

.navbar-default{
  background-color: #151515;
  border: 0 !important;
  border-radius: 0;
  padding: 15px;
  padding-bottom: 35px;
  margin-bottom: 0;
}

.back{
  background: none;
  border: none;
}

.navbar-brand > img{
  width: 200px;
  margin-left: -20px;
}

.navbar-right>li>a{
  font-weight: bold !important;
  color: #fff !important;
  margin: 20px 15px;
  font-size: 14px;
  text-align: center;
}

.navbar{
  border: none !important;
}

.navbar-default .navbar-toggle .icon-bar{
  background-color: #fff;
}

.contact-nav{
  border-radius: 46px;
  margin-top: 22px !important;
  padding: 10px !important;
  border: 3px solid #ffffff;
}


.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover{
  background-color: transparent;
}

.navbar-toggle{
  margin-top: 25px;
  border: none;
  color: #fff;
}

.navbar-default .navbar-collapse, .navbar-default .navbar-form{
  border-color: #151515;
  border: none !important;
}

/* hero section */

img{
  max-width: 100%;
}

.hero-img{
  margin: -10px auto auto 40px;
}

.hero{
  color: #151515;
  background-color: #fff;
  height: 100%;
  padding-bottom: 80px;
}

.hero-tag>strong{
  margin-left: 3px;
}

.buttons{
  margin: 65px auto !important;
}

.buttons>a:first-child{
  background-color: #151515;
  color: #fff;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 5px;
  margin-right: 25px;
  border: 3px solid #151515;
}
.buttons>a:nth-child(even){
  background-color: transparent;
  border: 2px solid #151515;
  color: #000;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 8px;
}

.socials>a,.socials>a:hover,.socials>a:focus,.socials>a:active{
  color: #151515;
  text-decoration: none !important;
  margin-right: 30px;
}

.hero-tag{
  font-size: 36px;
}

.about-tag{
  font-size: 16px;
  margin: 45px auto 45px;
  line-height: 35px;
}

.container{
  width: 70%;
}

/* work section */

.work{
  color: #151515;
  text-align: center;
  /* padding-bottom: 50px; */
  /* height: 100vh; */
}

.work-p{
  font-size: 16px;
  text-align: left;
  line-height: 30px;
  margin: -20px auto 30px;
}

.work-tag,.work-name{
  font-weight: bold;
}

.back1{
  margin-right: 85px;
}

.work-tag{
  margin: 30px auto;
  font-size: 32px;
  text-align: left;
}


.work-company{
  letter-spacing: 0.24px;
  font-weight: lighter;
  margin-top: 15px;
  font-size: 14px;
  padding-left: 2px;
}

.work-name{
  font-size: 29px;
}

.thumbnail,.thumbnail:hover,.thumbnail:focus,.thumbnail:active{
  border-radius: 20px;
  padding: 75px 15px;
  color: #151515;
  border: 0px;
  cursor: pointer;
  background-color: #f9f9f9 !important;
  text-decoration: none !important
}

.ui-thumb>h1{
  font-weight: bold;
  font-size: 24px;
}

.ui-thumb{
  margin-bottom: 45px;
}

/* testimonial section */
.testimonial{
  /* height: 100vh; */
  background-color: #fff;
  padding-bottom: 10px;
}

.carousel{
  text-align: center;
  color: #151515;
}

.item{
  min-height: 450px 
}

/* .carousel .item{
  min-height: 280px; 
} */

.test-p{
  width: 70%;
  margin: 25px auto;
  line-height: 30px;
  font-size: 18px;
}

.test-h{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
}

.test-img{
  margin : 0 auto 50px;
  border-radius: 50%;
  width: 144px;
  /* border: 5px solid #fff; */
}

.carousel-control.right,.carousel-control.left{
  background-image: none;
  color: #151515;
}

.carousel-indicators>li,.carousel-indicators .active{
  margin-top: 50vh !important ;
  margin-right: 5px;
}

.carousel-indicators>li{
  opacity: 0.5;
  background-color: #151515; 
}

.carousel-indicators>.active{
  opacity: 1;
  background-color: #151515;
}

.carousel-control{
  margin-top: 05vh;
}

.test-hr{
  border: 0.5px solid #151515;
  /* margin-top: 10vh; */
  width: 85%;
  
  margin: 5vh auto 
}

.bottom{
  color: #151515;
  background-color: #fff;
  text-decoration: none;
  /* box-shadow: 0 -1px 4px 3px rgba(0, 0, 0, 0.5); */
  width: 100%;
  padding-left: 100px;
  padding-bottom: 50px;
}

.bottom-h3{
  font-weight: bold;
  margin-top: 25px;
}

.bottom-h3>a,.bottom-a>a{
  color: #151515; 
  text-decoration: none;
}

.bottom-icons>a{
  color :#151515;
  margin-right: 15px;
}

.back{
  color: #151515;
  text-transform: uppercase;
  font-weight: 500;
  text-decoration: none;
  margin-top: 50px !important;
}

